import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';

@Component({
    selector: 'jam-single-warning',
    template: `<mat-card class="mat-card-flat yellow-bg-400 width-100"
    *ngIf="message"
    [ngStyle]="custom_styles"
>
    <span>{{ message }}</span>
    <a
        [routerLink]="link"
        [queryParams]="linkQueryParams || {}"
        *ngIf="link"
        >
        {{ linkText || 'MÃ¡s informaciÃ³n' }}
    </a>
    <a
        [href]="externalLink"
        target="_blank"
        *ngIf="externalLink"
        >
        {{ linkText || 'MÃ¡s informaciÃ³n' }}
    </a>

    <button
        *ngIf="actionButtonText"
        mat-button
        type="button"
        name="button"
        (click)="actionButtonClick.emit()"
        >
        {{ actionButtonText }}
    </button>
    <button
        *ngIf="actionIconButton"
        mat-icon-button
        [matTooltip]="actionIconButtonTooltip"
        type="button"
        name="button"
        (click)="actionIconButtonClick.emit()"
        >
        <mat-icon>
            {{ actionIconButton }}
        </mat-icon>
    </button>
</mat-card>
`,
    styles: [`.yellow-bg-400{box-sizing:border-box;background:#ffee58;color:#212121}`]
})
export class SingleWarningComponent implements OnInit {
    @Input() public message: string;
    @Input() public backgroundColor: string;
    @Input() public textColor: string;
    @Input() public link: string;
    @Input() public linkQueryParams: {[key: string]: string};
    @Input() public externalLink: string;
    @Input() public linkText: string;
    @Input() public actionButtonText: string;
    @Input() public actionIconButton: string;
    @Input() public actionIconButtonTooltip: string;
    @Output() public actionButtonClick: EventEmitter<void> = new EventEmitter<void>();
    @Output() public actionIconButtonClick: EventEmitter<void> = new EventEmitter<void>();

    public custom_styles: {
        color?: string;
        'background-color'?: string;
    } = {};

    public ngOnInit() {
        if (this.backgroundColor) {
            this.custom_styles['background-color'] = this.backgroundColor;
        }
        if (this.textColor) {
            this.custom_styles.color = this.textColor;
        }
    }

}
