import { Component, Inject, HostListener } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Resource } from 'ngx-jsonapi';

export interface IEditTextAttributeData {
    resource: Resource;
    attribute: string;
    title: string;
    accept?: string;
    message?: string;
    textarea_label?: string;
}

@Component({
    selector: 'jam-edit-text-attribute',
    template: `<form name="myForm" novalidate (ngSubmit)="updateAttributeAndClose(data.attribute, text_value)">
    <h2 *ngIf="data.title" mat-dialog-title [innerHtml]="data.title"></h2>
    <mat-dialog-content>
        <p *ngIf="data.message">{{ data.message }}</p>
        <mat-form-field
            appearance="outline"
            fxFlex
        >
            <mat-label>{{ data.textarea_label }}</mat-label>
            <textarea maxlength="140"
                name="text_attribute"
                type="text"
                #textarea
                [(ngModel)]="text_value"
                matInput
            ></textarea>
            <mat-hint align="end">{{textarea.value.length}} / 140</mat-hint>
        </mat-form-field>
    </mat-dialog-content>

    <mat-dialog-actions fxLayout="row" fxLayoutAlign="end center">
        <jam-submit
            (cancel)="dialogRef.close()"
            [submitLabel]="data.accept"
        ></jam-submit>
    </mat-dialog-actions>
</form>
`
})
export class EditTextAttributeDialogComponent {
    public text_value = '';

    public constructor(
        public dialogRef: MatDialogRef<EditTextAttributeDialogComponent>,
        @Inject(MAT_DIALOG_DATA) public data: IEditTextAttributeData) {
        if (!data.accept) {
            data.accept = 'Aceptar';
        }
    }

    @HostListener('window: keyup', ['$event']) public onKeyUp(event: KeyboardEvent) {
        if (event.key === 'Enter' && !event.shiftKey)   {
            this.updateAttributeAndClose(this.data.attribute, this.text_value);
        }
    }

    public updateAttributeAndClose(attribute: string, value: string) {
        this.data.resource.attributes[attribute] = value;
        this.dialogRef.close(true);
    }
}
